import CoinPairInfo from '@/components/common/CoinPairInfo'
import VaultsTableAction from '@/components/vaults-v2/common/ProfileActions'
import VaultsHoldings from '@/components/vaults-v2/list/common/VaultsHoldings'
import VaultModal from '@/components/vaults-v2/modal'
import useGetPythTokenPrice from '@/hooks/vault-v2/pyth-price/useGetPythTokenPrice'
import useVaultList from '@/hooks/vault-v2/useVaultList'
import useActiveOrdersStore from '@/store/profile/activeOrders'
import useVaultsActionStore from '@/store/vaults-v2/useVaultsAction'
import useVaultsListV2Store from '@/store/vaults-v2/useVaultsList'
import useVaultsPositionStore from '@/store/vaults-v2/useVaultsPosition'
import useVaultsPythPriceStore from '@/store/vaults-v2/useVaultsPythPrice'
import { CetusTooltip, SelectTab, TableSortTh } from '@cetus/design'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import useTokenPriceStore from '@cetus/stores/src/tokenPrice'
import { H5MapTable, NoData, SingleCoinImage, Table } from '@cetus/ui-kit'
import { d, formatCurrency, formatNumber, isAvailableObject, symbolDataDisplayProcessing } from '@cetus/utils'
import { Box, Center, HStack, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import HiddenDotted from '../HiddenDotted'

function ProfileVaults({ selectCoinList = [] }: { selectCoinList: any }) {
  const { vaultsPositionObj } = useVaultsPositionStore()
  const { vaultPageList, vaultListLoading, setVaultListLoading } = useVaultsListV2Store()
  const { filterVaultList, getCurrentTabNum } = useVaultList()
  const [pageList, setPageList] = useState([])

  // Â¢ûÂä†ÊåÅ‰ªì Âíå Âç†ÊØî ÊéíÂ∫è
  type sortByType = {
    label: 'Your Holdings' | 'Share of Pool' | ''
    value: 'holdings' | 'shareOfPool' | ''
  }
  const [sortRule, setSortRule] = useState<'desc' | 'asc'>('desc')
  const [sortBy, setSortBy] = useState<sortByType>({
    label: '',
    value: ''
  })

  const { profileActionTab, isProfileOpenVaultModal, setIsProfileOpenVaultModal, profilePoolInfo } = useVaultsActionStore()
  useEffect(() => {
    if (sortBy.value == '') {
      setPageList(vaultPageList)
    }
  }, [vaultPageList])

  console.log('üöÄ ~ ProfileVaults ~ pageList:', vaultListLoading, pageList?.length === 0, pageList)
  const { isAutoRefresh, autoRefreshCount } = useActiveOrdersStore()
  const { isApp } = useWindowWidth()
  const isYourHoldings = true
  const queryOptions: any = {
    sortRule: 'desc',
    sortType: 'tvl'
  }

  const tabList = useMemo(() => {
    const list: Tab<object>[] = [
      {
        label: 'All Vault',
        value: 'all',
        num: getCurrentTabNum({ sortOptions: queryOptions, currentTab: 'all', isYourHoldings, selectCoinList }) || ''
      },
      {
        label: 'Cetus',
        value: 'cetus',
        num: getCurrentTabNum({ sortOptions: queryOptions, currentTab: 'cetus', isYourHoldings, selectCoinList }) || ''
      },
      {
        label: 'Haedal',
        value: 'haedal',
        num: getCurrentTabNum({ sortOptions: queryOptions, currentTab: 'haedal', isYourHoldings, selectCoinList }) || ''
      }
    ]
    return list
  }, [JSON.stringify(pageList), isYourHoldings, JSON.stringify(selectCoinList)])

  const [currentTab, setCurrentTab] = useState('all')

  const { coinPriceObj } = useTokenPriceStore()
  const { getTokenAmountValue } = useTokenPrice()

  const { getTokenAmountValueByPyth } = useGetPythTokenPrice()
  const { pythPriceMap } = useVaultsPythPriceStore()

  const handleTabChange = (value: string) => {
    if (currentTab === value || vaultListLoading) return
    setCurrentTab(value)
    filterVaultList({ sortOptions: queryOptions, currentTab: value as string, isYourHoldings, selectCoinList })
    setVaultListLoading(true)
    setTimeout(() => {
      setVaultListLoading(false)
    }, 200)
  }

  useEffect(() => {
    console.log('üöÄ ~ useEffect ~ vaultsPositionObj:', vaultsPositionObj)
    if (isAvailableObject(vaultsPositionObj)) {
      filterVaultList({ sortOptions: queryOptions, currentTab, isYourHoldings, selectCoinList })
    }
  }, [vaultsPositionObj, selectCoinList])

  const getAmountValue = (vault: any, displayCoinType: string, displayAmount: string | undefined) => {
    return vault.category == 'haedal'
      ? getTokenAmountValueByPyth(displayCoinType, displayAmount)
      : getTokenAmountValue(displayCoinType, displayAmount, '--')
  }
  useEffect(() => {
    if (vaultPageList?.length > 0 && sortBy.value !== '') {
      const result = vaultPageList.map((vault: any) => {
        const vaultsPosition = vaultsPositionObj[vault?.vaultId] || {}

        const holdCoinAValue = getAmountValue(vault, vaultsPosition.displayCoinTypeA, vaultsPosition.displayAmountA)
        const holdCoinBValue = getAmountValue(vault, vaultsPosition.displayCoinTypeB, vaultsPosition.displayAmountB)
        const holdingAmount =
          holdCoinAValue == '--' || holdCoinAValue == '--'
            ? '0'
            : d(holdCoinAValue)
                .plus(holdCoinBValue || 0)
                .toString()
        const holdingAmountDisplay = symbolDataDisplayProcessing(holdingAmount, '$')

        return {
          ...vault,
          sharePoolRate: vaultsPosition?.sharePoolRate,
          holdingAmount: parseFloat(holdingAmount || '0'),
          holdingAmountDisplay
        }
      })

      const sorted = result.sort((a, b) =>
        sortRule === 'asc'
          ? sortBy.value == 'holdings'
            ? a.holdingAmount - b.holdingAmount
            : a.sharePoolRate - b.sharePoolRate
          : sortBy.value == 'holdings'
            ? b.holdingAmount - a.holdingAmount
            : b.sharePoolRate - a.sharePoolRate
      )
      console.log('üöÄ ~ useEffect ~ sorted:', vaultsPositionObj, sorted)
      setPageList(sorted)
    }
  }, [vaultPageList.length, sortBy.value, sortRule, coinPriceObj, pythPriceMap])

  const clickSort = (item: sortByType) => {
    if (isApp) {
      if (item?.value !== sortBy?.value) {
        setSortRule('desc')
        setSortBy(item)
      }
    } else {
      if (item?.value == sortBy?.value) {
        const rule = sortRule == 'desc' ? 'asc' : 'desc'
        setSortRule(rule)
      } else {
        setSortRule('desc')
        setSortBy(item)
      }
    }
  }
  const sortByList = [
    { label: 'Your Holdings', value: 'holdings' },
    { label: 'Share of Pool', value: 'shareOfPool' }
  ]
  const sortByObject = sortByList.reduce((obj: any, item) => {
    obj[item.value] = item
    return obj
  }, {})

  const columns = getColumns({
    isShowPowered: currentTab !== 'Haedal',
    vaultsPositionObj,
    getAmountValue,
    sortRule,
    sortBy,
    sortByObject,
    clickSort
  })

  return (
    <VStack w="100%" justifyContent="flex-start" alignItems="flex-start">
      <SelectTab
        type="outlineTab"
        tabList={tabList}
        currentTab={currentTab}
        handleChangeTab={(item: any) => handleTabChange(item?.value)}
        isActive={(current, tab) => current === tab.value}
        wrapStyle={{
          border: 'none',
          h: '32px',
          gap: '8px',
          bg: 'none'
        }}
        itemStyle={{
          fontSize: '14px',
          borderRadius: '8px',
          p: '0 8px'
        }}
      />
      <VStack w="100%" position="relative" gap="20px">
        {(isAutoRefresh || !vaultListLoading) && pageList?.length === 0 ? (
          <NoData type="nodata" text="No vaults found" noBorder />
        ) : isApp ? (
          <VStack w="100%" gap="4px">
            <H5MapTable
              rowKey="clmmPoolAddress"
              columns={columns}
              dataSource={pageList}
              loading={!isAutoRefresh && vaultListLoading}
              itemSkeletonLength={5}
              itemHeight="30px"
            />
          </VStack>
        ) : (
          <Table
            rowKey="clmmPoolAddress"
            columns={columns}
            dataSource={pageList}
            loading={!isAutoRefresh && vaultListLoading}
            rowStyle={{ h: '80px', cursor: 'pointer' }}
          />
        )}
      </VStack>
      {isProfileOpenVaultModal && (
        <VaultModal
          key={profilePoolInfo.vaultId}
          isOpen={isProfileOpenVaultModal}
          setIsOpen={setIsProfileOpenVaultModal}
          displayTokenA={profilePoolInfo.displayTokenA}
          displayTokenB={profilePoolInfo.displayTokenB}
          feeDisplay={profilePoolInfo.feeDisplay}
          clmmPool={profilePoolInfo.clmmPoolAddress}
          category={profilePoolInfo.category}
          vaultId={profilePoolInfo.vaultId}
          isReverse={profilePoolInfo.isReverse}
          currentTab={profileActionTab}
        />
      )}
    </VStack>
  )
}
const getColumns = ({
  isShowPowered,
  vaultsPositionObj,
  getAmountValue,
  sortRule,
  sortBy,
  sortByObject,
  clickSort
}: {
  isShowPowered: boolean
  vaultsPositionObj: any
  getAmountValue: (record: any, coin_type: string, amount: string) => void
  sortRule: 'asc' | 'desc'
  sortBy: any
  sortByObject: any
  clickSort: (value: any) => void
}) => [
  {
    title: <Text>Pools</Text>,
    key: 'pools',
    thConfig: {
      pl: '0px !important'
    },
    render: (record: any) => {
      return <CoinPairInfo poolInfo={{ ...record, poolAddress: record?.vaultId }} isShowPowered={isShowPowered} symbolFontSize="15px" />
    }
  },
  {
    title: <TableSortTh labelInfo={sortByObject['holdings']} sortBy={sortBy} sortRule={sortRule} clickSort={(value: any) => clickSort(value)} />,
    key: 'holdings',
    render: (record: any) => {
      const vaultsPosition = vaultsPositionObj[record?.vaultId] || {}
      const amountValueA = getAmountValue(record, record?.displayTokenA?.coin_type, vaultsPosition?.displayAmountA)
      const amountValueB = getAmountValue(record, record?.displayTokenB?.coin_type, vaultsPosition?.displayAmountB)
      return (
        <CetusTooltip
          placement="top"
          tooltip={
            <VStack gap="8px" align="flex-start" minW="200px">
              <HStack w="100%" gap="12px" justify="space-between">
                <HStack>
                  <SingleCoinImage imageUrl={record.displayTokenA.logo_url} imgBoxStyle={{ w: '20px', h: '20px' }} />
                  <Text color="text_caption" fontSize="12px">
                    {record?.displayTokenA?.symbol}
                  </Text>
                </HStack>
                <HiddenDotted>
                  <VStack align="flex-end">
                    <Text color="text_caption" fontSize="12px">
                      {formatNumber(vaultsPosition?.displayAmountA)}
                    </Text>
                    <Text fontSize="12px">{amountValueA == '--' ? '$--' : formatCurrency(amountValueA, 2)}</Text>
                  </VStack>
                </HiddenDotted>
              </HStack>
              <Box w="100%" height="1px" bg="border" />
              <HStack w="100%" gap="12px" justify="space-between">
                <HStack>
                  <SingleCoinImage imageUrl={record.displayTokenB.logo_url} imgBoxStyle={{ w: '20px', h: '20px' }} />
                  <Text color="text_caption" fontSize="12px">
                    {record?.displayTokenB?.symbol}
                  </Text>
                </HStack>
                <HiddenDotted>
                  <VStack align="flex-end">
                    <Text color="text_caption" fontSize="12px">
                      {formatNumber(vaultsPosition?.displayAmountB)}
                    </Text>
                    <Text fontSize="12px">{amountValueB == '--' ? '$--' : formatCurrency(amountValueB, 2)}</Text>
                  </VStack>
                </HiddenDotted>
              </HStack>
            </VStack>
          }
        >
          <Center as="button" ml="4px">
            <HiddenDotted>
              <HStack justifyContent="flex-end" gap="0">
                <Text color="text_caption" mr="2px" textDecoration={'underline dotted'} textUnderlineOffset="3px">
                  {`${vaultsPosition?.balanceDisplay || '0'} LP`}
                </Text>
                <Text>(</Text>
                <VaultsHoldings color="text_paragraph" vaultId={record.vaultId} category={record.category} />
                <Text>)</Text>
              </HStack>
            </HiddenDotted>
          </Center>
        </CetusTooltip>
      )
    }
  },
  {
    title: <TableSortTh labelInfo={sortByObject['shareOfPool']} sortBy={sortBy} sortRule={sortRule} clickSort={(value: any) => clickSort(value)} />,
    key: 'shareOfPool',
    render: (record: any) => {
      const vaultsPosition = vaultsPositionObj[record?.vaultId] || {}
      console.log('üöÄ ~ vaultsPositionObj:', vaultsPositionObj, vaultsPosition)
      return (
        <HStack justify="flex-end">
          <HiddenDotted>
            <Text color="text_caption"> {vaultsPosition?.shartOfPoolDisplay}</Text>
          </HiddenDotted>
        </HStack>
      )
    }
  },
  {
    title: <Text textAlign="right">Actions</Text>,
    key: 'actions',
    render: (record: any) => {
      return <VaultsTableAction poolInfo={record} />
    }
  }
]

export default ProfileVaults
